<template>
  <div class="app">
    <Header></Header>
    <span class="reg" v-if="isAuth">{{ name }}</span>
    <button class="reg" @click="login" v-if="!isAuth">Войти</button>
    <Grid :data="gridData" :columns="gridColumns"></Grid>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import Grid from "@/components/Grid.vue";
import Keycloak from "keycloak-js";

export default {
  props: {
    keycloak: Keycloak,
  },
  components: {
    Header,
    Grid,
  },
  data() {
    return {
      name: "",
      isAuth: false,
      gridColumns: [
        "ФИО",
        "Место работы",
        "Должность",
        "Ставка",
        "Часы на ставку",
        "Планируемое количеаство часов",
        "Фактическое количество часов",
        "Разница между плановым и фактическим количеством часов",
        "Год",
      ],
      gridData: [
        {
          ФИО: "Сапожникова Александра Вячеславовна",
          "Место работы":
            "Кафедра иностранных языков и образовательных технологий",
          Должность: "Старший преподаватель",
          Ставка: "1.0",
          "Часы на ставку": "100",
          "Планируемое количеаство часов": "64",
          "Фактическое количество часов": "64",
          "Разница между плановым и фактическим количеством часов": "0",
          Год: "2020",
        },
        {
          ФИО: "Зубова Анастасия Владимировна",
          "Место работы": "Кафедра интеллектуальных информационных технологий",
          Должность: "Доцент",
          Ставка: "1.0",
          "Часы на ставку": "120",
          "Планируемое количеаство часов": "64",
          "Фактическое количество часов": "60",
          "Разница между плановым и фактическим количеством часов": "4",
          Год: "2019",
        },
        {
          ФИО: "Шегал Анна Айзиковна",
          "Место работы": "Департамент радиоэлектроники и связи",
          Должность: "Доцент",
          Ставка: "0.375",
          "Часы на ставку": "90",
          "Планируемое количеаство часов": "44",
          "Фактическое количество часов": "30",
          "Разница между плановым и фактическим количеством часов": "14",
          Год: "2021",
        },
      ],
    };
  },
  methods: {
    login() {
      if (!this.keycloak.authenticated) {
        this.keycloak.login();
      }
      else {
        this.name = this.keycloak.tokenParsed.user.person.title;
        isAuth = true;
      }
    },
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.reg {
  display: flex;
  margin-top: 15px;
  margin-left: 15px;
  font-size: 20px;
}
